@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;1,300&display=swap);
.topbar{display:flex;flex-direction:row;position:fixed;width:100vw;z-index:9;height:50px;background-color:#eceff4;padding:0 20px;box-shadow:rgba(50,50,93,.25) 0px 6px 12px -2px,rgba(0,0,0,.3) 0px 3px 7px -3px}.topbar__header{display:flex;flex-direction:row;align-items:center;height:100%}.topbar__mobile_button{display:none;margin-left:auto;font-size:1.5rem;cursor:pointer;outline:none}.topbar__menu{margin-left:auto;display:flex;flex-direction:row;justify-content:center;align-items:center;background-color:#eceff4}.topbar__menu__item{text-decoration:none;color:#2e3440;padding:10px;border-radius:5px;text-align:center}.topbar__menu__item--active{background-color:#81a1c1;color:#eceff4}@media screen and (max-width: 750px){.topbar{flex-direction:column}.topbar__menu{flex-direction:column;position:absolute;top:50px;left:0;width:100vw}.topbar__menu--show{display:flex}.topbar__menu--hide{display:none}.topbar__mobile_button{display:block}}
.about__card{padding:20px}.about__objectives{display:flex;flex-direction:row}.about__objectives__image{flex-basis:50%;width:50%}.about__objectives__image img{width:100%;height:auto}.about__objectives__text{flex-basis:50%;background-color:#d8dee9;color:#2e3440}.about__objectives__text h2{font-weight:500;font-size:1.5rem;margin-bottom:10px}.about .about__contact{display:flex;flex-direction:row}.about .about__contact__image{flex-basis:50%;width:50%}.about .about__contact__image img{width:100%;height:auto}.about .about__contact__text{flex-basis:50%;background-color:#eceff4;color:#2e3440}.about .about__contact__text h2{font-weight:500;font-size:1.5rem;margin-bottom:10px}
.card{background-color:#5e81ac;width:100%;height:350px;position:relative}.card__overlay{background-color:rgba(0,0,0,.3);width:100%;position:absolute;bottom:0;display:flex;flex-direction:row;color:#eceff4;font-size:1.2rem;padding:15px}.card__overlay__content__title{font-weight:500}.card__overlay__content__text{font-weight:300}.card__overlay__actions{display:flex;align-items:center;margin-left:auto}.card__overlay__actions button{border:3px solid #eceff4;border-radius:5px;outline:none;color:#eceff4;background:none;cursor:pointer;padding:5px}
.hide{display:none}.modal__overlay{position:absolute;width:100%;height:100%;background-color:rgba(0,0,0,.5);top:0;left:0;z-index:99}.modal__content{border-radius:5px;position:absolute;width:70vw;height:500px;background-color:#eceff4;top:100px;left:50%;transform:translateX(-50%);z-index:999}.modal__content__header{border-top-left-radius:5px;border-top-right-radius:5px;padding:8px 10px;display:flex;flex-direction:row;background-color:#d8dee9}.modal__content__header__close{margin-left:auto;cursor:pointer}.modal__content__children{padding:10px}
.imobiliarias{display:flex;flex-direction:row;margin-top:20px}.imobiliarias__item img{width:100px;height:auto}.select{width:100%}
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}html,body{height:100%;font-family:"Roboto";font-weight:300;background-color:#eceff4;font-size:14px}*{box-sizing:border-box}
