@import "~nord/src/sass/nord.scss";

.hide {
  display: none;
}
.modal {
  &__overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(black, 0.5);
    top: 0;
    left: 0;
    z-index: 99;
  }

  &__content {
    border-radius: 5px;
    position: absolute;
    width: 70vw;
    height: 500px;
    background-color: $nord6;
    top: 100px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 999;

    &__header {
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      padding: 8px 10px;
      display: flex;
      flex-direction: row;
      background-color: $nord4;

      &__close {
        margin-left: auto;
        cursor: pointer;
      }
    }

    &__children {
      padding: 10px;
    }
  }
}
