@import "~nord/src/sass/nord.scss";

.card {
  background-color: $nord10;
  width: 100%;
  height: 350px;
  position: relative;

  &__overlay {
    background-color: rgba(0, 0, 0, 0.3);
    width: 100%;
    position: absolute;
    bottom: 0;
    display: flex;
    flex-direction: row;
    color: $nord6;
    font-size: 1.2rem;
    padding: 15px;

    &__content {
      &__title {
        font-weight: 500;
      }
      &__text {
        font-weight: 300;
      }
    }

    &__actions {
      display: flex;
      align-items: center;
      margin-left: auto;
      button {
        border: 3px solid $nord6;
        border-radius: 5px;
        outline: none;
        color: $nord6;
        background: none;
        cursor: pointer;
        padding: 5px;
      }
    }
  }
}
