@import "~nord/src/sass/nord.scss";

.about {
  &__card {
    padding: 20px;
  }

  &__objectives {
    display: flex;
    flex-direction: row;

    &__image {
      flex-basis: 50%;
      width: 50%;
      img {
        width: 100%;
        height: auto;
      }
    }
    &__text {
      flex-basis: 50%;
      background-color: $nord4;
      color: $nord0;
      h2 {
        font-weight: 500;
        font-size: 1.5rem;
        margin-bottom: 10px;
      }
    }
  }

  .about__contact {
    display: flex;
    flex-direction: row;

    &__image {
      flex-basis: 50%;
      width: 50%;
      img {
        width: 100%;
        height: auto;
      }
    }
    &__text {
      flex-basis: 50%;
      background-color: $nord6;
      color: $nord0;
      h2 {
        font-weight: 500;
        font-size: 1.5rem;
        margin-bottom: 10px;
      }
    }
  }
}
