@import "~nord/src/sass/nord.scss";

.topbar {
  display: flex;
  flex-direction: row;
  position: fixed;
  width: 100vw;
  z-index: 9;
  height: 50px;
  background-color: $nord6;
  padding: 0 20px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.topbar__header {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;
}

.topbar__mobile_button {
  display: none;
  margin-left: auto;
  font-size: 1.5rem;
  cursor: pointer;
  outline: none;
}

.topbar__menu {
  margin-left: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  background-color: $nord6;

  &__item {
    text-decoration: none;
    color: $nord0;
    padding: 10px;
    border-radius: 5px;
    text-align: center;

    &--active {
      background-color: $nord9;
      color: $nord6;
    }
  }
}

@media screen and (max-width: 750px) {
  .topbar {
    flex-direction: column;
  }
  .topbar__menu {
    flex-direction: column;
    position: absolute;
    top: 50px;
    left: 0;
    width: 100vw;
    &--show {
      display: flex;
    }

    &--hide {
      display: none;
    }
  }
  .topbar__mobile_button {
    display: block;
  }
}
